@import url("https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,200;0,300;0,400;0,600;0,700;1,200;1,300&display=swap");
:root {
  --main-color:#e8786e;
  --black:#555;
  --light-color:#777;
  --bg:#fceae9;
  --border:.1rem solid rgba(0,0,0,.1);
  --box-shadow:0 .5rem 1.5rem rgba(0,0,0,.1);
}

* {
  font-family: "Nunito", sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
  border: none;
  text-decoration: none;
  text-transform: capitalize;
  transition: all 0.3s ease-out;
}

html {
  font-size: 62.5%;
  scroll-behavior: smooth;
  scroll-padding-top: 9rem;
  overflow-x: hidden;
}
@media (max-width: 991px) {
  html {
    font-size: 55%;
  }
}
@media (max-width: 450px) {
  html {
    font-size: 50%;
  }
}

section {
  padding: 2rem 9%;
}
@media (max-width: 991px) {
  section {
    padding: 2rem;
  }
}

.box-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(27rem, 1fr));
  gap: 1.5rem;
}
.box-container .box {
  text-align: center;
  border: var(--border);
  box-shadow: var(--box-shadow);
  border-radius: 0.5rem;
  padding: 4rem 2rem;
}

.row {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 1.5rem;
}

.heading {
  text-align: center;
  padding-bottom: 3rem;
  font-size: 4rem;
  color: var(--black);
}
.heading span {
  color: var(--main-color);
  border-radius: 0.5rem;
  background: var(--bg);
  padding: 0 1.5rem;
}

#menu-btn {
  cursor: pointer;
  font-size: 2.5rem;
  padding: 1rem 1.3rem;
  border-radius: 0.5rem;
  color: var(--main-color);
  background: var(--bg);
  display: none;
}
@media (max-width: 768px) {
  #menu-btn {
    display: initial;
  }
}

#menu-btn:hover {
  color: #fff;
  background: var(--main-color);
}

.btn {
  margin-top: 1rem;
  display: inline-block;
  padding: 0.8rem 3rem;
  font-size: 1.7rem;
  cursor: pointer;
  color: #fff;
  background: var(--main-color);
  border-radius: 0.5rem;
  box-shadow: var(--box-shadow);
  position: relative;
  z-index: 0;
  overflow: hidden;
}
.btn:hover::before {
  left: 0;
  width: 100%;
}
.btn::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  width: 0%;
  background: var(--black);
  z-index: -1;
  transition: 0.2s ease-out;
}

.header {
  position: fixed;
  display: flex;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  background: #fff;
  box-shadow: var(--box-shadow);
  align-items: center;
  justify-content: space-between;
  padding: 2rem 9%;
}
@media (max-width: 991px) {
  .header {
    padding: 2rem;
  }
}
.header__logo {
  font-size: 2.5rem;
  color: var(--black);
  font-weight: bolder;
}
.header__navbar--item {
  font-size: 1.7rem;
  color: var(--light-color);
  margin-left: 2rem;
}
@media (max-width: 768px) {
  .header__navbar--item {
    font-size: 2rem;
    display: block;
    padding: 1rem;
    margin: 1rem;
    border-radius: 0.5rem;
  }
  .header__navbar--item:hover {
    background: var(--bg);
  }
}
.header__navbar--item:hover {
  color: var(--main-color);
}
@media (max-width: 768px) {
  .header__navbar {
    position: absolute;
    top: 115%;
    right: 2rem;
    background: #fff;
    box-shadow: var(--box-shadow);
    border: var(--border);
    border-radius: 0.5rem;
    width: 30rem;
    transform: scale(0);
    transform-origin: top right;
    opacity: 0;
  }
}

.active {
  transform: scale(1);
  opacity: 1;
}

.home {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 1.5rem;
  padding-top: 10rem;
  background: var(--bg);
  position: relative;
}
.home__content {
  flex: 1 1 45rem;
}
@media (max-width: 768px) {
  .home__content {
    text-align: center;
  }
}
.home__content h3 {
  font-size: 3.5rem;
  color: var(--black);
  line-height: 1.8;
}
.home__content p {
  font-size: 1.7rem;
  color: var(--light-color);
  line-height: 1.8;
  padding: 1rem 0;
}
.home__image {
  flex: 1 1 45rem;
}
.home__image img {
  width: 100%;
}
.home__cloud {
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
  height: 18rem;
  background-size: 250rem 18rem;
  background: url(../../src/images/cloud.png);
  animation: cloud 30s linear infinite;
}
.home__cloud-1 {
  opacity: 0.5;
  height: 20rem;
  animation-duration: 35s;
}

@keyframes cloud {
  0% {
    background-position-x: 0rem;
  }
  100% {
    background-position-x: -250rem;
  }
}
.services .box-container .box img {
  height: 10rem;
}
.services .box-container .box h3 {
  color: var(--black);
  font-size: 2.5rem;
  padding-top: 1.5rem;
}
.services .box-container .box p {
  color: var(--light-color);
  font-size: 1.5rem;
  padding: 1rem 0;
  line-height: 1.8;
}

.about__image {
  flex: 1 1 45rem;
  padding: 2rem;
}
.about__image img {
  width: 100%;
}
.about__content {
  flex: 1 1 45rem;
}
.about__content--title {
  font-size: 3rem;
  color: var(--black);
}
.about__content p {
  font-size: 1.5rem;
  color: var(--light-color);
  line-height: 1.8;
  padding: 1rem 0;
}
.about__content--icons-container {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  padding-top: 2rem;
}
.about__content--icons-container .icons {
  flex: 1 1 15rem;
  border-radius: 0.5rem;
  border: var(--border);
  padding: 1.5rem;
  text-align: center;
}
.about__content--icons-container .icons i {
  border-radius: 50%;
  background: var(--bg);
  color: var(--main-color);
  height: 5rem;
  width: 5rem;
  line-height: 5rem;
  font-size: 2rem;
}
.about__content--icons-container .icons h3 {
  color: var(--light-color);
  font-size: 1.6rem;
  padding-top: 1rem;
}

.portfolio .box-container .box {
  height: 30rem;
  border-radius: 0.5rem;
  overflow: hidden;
  position: relative;
  box-shadow: var(--box-shadow);
  padding: 0;
}
.portfolio .box-container .box:hover .content {
  top: 0;
  opacity: 1;
}
.portfolio .box-container .box img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}
.portfolio .box-container .box span {
  position: absolute;
  top: 1rem;
  right: 2rem;
  font-weight: bolder;
  font-size: 5rem;
  color: var(--black);
}
.portfolio .box-container .box .content {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 6rem;
  left: 0;
  transition-delay: 0.3s;
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;
  opacity: 0;
}
.portfolio .box-container .box .content h3 {
  font-size: 2.5rem;
  padding-bottom: 0.5rem;
  color: var(--black);
}
.portfolio .box-container .box .content p {
  font-size: 1.5rem;
  color: var(--light-color);
}
.portfolio .box-container .box::before {
  content: "";
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: #fff;
  clip-path: circle(30% at 93% 0);
  transition: 0.3s linear;
}
.portfolio .box-container .box:hover::before {
  clip-path: circle(100%);
}

.pricing .box-container {
  grid-template-columns: repeat(auto-fit, minmax(30rem, 1fr));
}
.pricing .box-container .box {
  padding: 2rem;
}
.pricing .box-container .box h3 {
  color: vara(--black);
  font-size: 2.5rem;
}
.pricing .box-container .box img {
  margin: 2.5rem 0;
  height: 15rem;
}
.pricing .box-container .box .price {
  font-size: 4rem;
  font-weight: bolder;
  color: var(--main-color);
}
.pricing .box-container .box ul {
  padding: 1rem;
  list-style: none;
}
.pricing .box-container .box ul li {
  padding: 1rem 0;
  font-size: 1.7rem;
  color: var(--light-color);
}

.review .box-container {
  grid-template-columns: repeat(auto-fit, minmax(30rem, 1fr));
}
.review .box-container .box {
  padding: 2rem;
  text-align: start;
}
.review .box-container .box .user {
  display: flex;
  align-items: center;
  padding-bottom: 1rem;
}
.review .box-container .box .user img {
  height: 7rem;
  width: 7rem;
  border-radius: 50%;
  margin-right: 1rem;
}
.review .box-container .box .user h3 {
  font-size: 2.2rem;
  color: var(--black);
  padding-bottom: 0.5rem;
}
.review .box-container .box .user i {
  font-size: 1.5rem;
  color: var(--main-color);
}
.review .box-container .box .user .fa-quote-right {
  margin-left: auto;
  font-size: 5rem;
  color: var(--bg);
}
.review .box-container .box p {
  padding-top: 1rem;
  font-size: 1.6rem;
  line-height: 1.8;
  color: var(--light-color);
  font-style: italic;
}

.contact .icons-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(30rem, 1fr));
  gap: 1.5rem;
}
.contact .icons-container .icons {
  padding: 2.5rem;
  text-align: center;
  border: var(--border);
  border-radius: 0.5rem;
  box-shadow: var(--box-shadow);
}
.contact .icons-container .icons i {
  height: 6rem;
  width: 6rem;
  line-height: 6rem;
  font-size: 2.5rem;
  color: var(--main-color);
  background: var(--bg);
  border-radius: 50%;
}
.contact .icons-container .icons h3 {
  color: var(--black);
  padding: 1rem 0;
  font-size: 2.5rem;
}
.contact .icons-container .icons p {
  color: var(--light-color);
  line-height: 1.8;
  font-size: 1.5rem;
}
.contact .row {
  margin-top: 2rem;
  align-items: unset;
}
.contact .row .map {
  flex: 1 1 45rem;
  width: 100%;
  padding: 2rem;
  border: var(--border);
  box-shadow: var(--box-shadow);
  border-radius: 0.5rem;
}
.contact .row form {
  flex: 1 1 45rem;
  padding: 2rem;
  border: var(--border);
  box-shadow: var(--border-shadow);
  border-radius: 0.5rem;
}
.contact .row form .box {
  margin: 0.7rem 0;
  padding: 1rem;
  font-size: 1.6rem;
  color: var(--black);
  border-radius: 0.5rem;
  border: var(--border);
  background: #f7f7f7;
  text-transform: none;
  width: 100%;
}
.contact .row form .box:focus {
  background: var(--bg);
}
.contact .row form textarea {
  height: 20rem;
  resize: none;
}
.contact .row form .btn:hover {
  background: var(--black);
}

.footer {
  margin-top: 1rem;
  background: var(--bg);
  padding-bottom: 9rem;
}
.footer .box-container {
  grid-template-columns: repeat(auto-fit, minmax(23rem, 1fr));
}
.footer .box-container .box {
  box-shadow: none;
  border: none;
  text-align: unset;
}
.footer .box-container .box h3 {
  font-size: 2.5rem;
  padding: 1rem 0;
  color: var(--black);
}
.footer .box-container .box a {
  display: block;
  font-size: 1.5rem;
  padding: 1rem 0;
  color: var(--light-color);
}
.footer .box-container .box a i {
  padding-right: 0.5rem;
  color: var(--main-color);
}
.footer .box-container .box a:hover i {
  padding-right: 2rem;
}
.footer .attribution {
  font-size: 1.5rem;
  color: var(--main-color);
  text-align: center;
}